section {
    width: 100vw;
    height: 100vh;
    display: flex;
}

.wrapper {
  display: flex;
  flex-direction: row;
  width: 500vw;
  transform: rotate(90deg) translateY(-100vh);
  transform-origin: top left;
}

.outer-wrapper {
  width: 100vh;
  height: 100vw;
  transform: rotate(-90deg) translateX(-100vh);
  transform-origin: top left;
  overflow-y: scroll;
  overflow-x: hidden;
  position: absolute;
}

::-webkit-scrollbar {
    display: none;
}

.one { 
  background: black; 
}

.two { 
  background: green; 
}

.three { 
  background: yellow; 
}
.four { 
  background: red; 
}
.five { 
  background: white; 
}